* {
  margin: 0;
}

html {
  --color-orange: orange;
}

.app {
  position: relative;
}

.nav h4 {
  padding: 30px;
  background-color: #000;
  color: #fff;
  margin: 0;
  font-size: 26px;
}

.list {
  text-align: left;
  padding: 5px 20px;
  border-bottom: 1px solid gray;
}

.list button {
  padding: 5px;
  margin: 10px;
}

.list h4 {
  display: inline-block;
}

.list h4,
p {
  padding: 10px;
}

.list h4 span {
  margin: 0 5px 0 15px;
}

.textfield {
  height: 30px;
  margin: 20px;
  border: 2px solid #9acd32;
  border-radius: 10px;
  overflow: hidden;
  display: flex;
  gap: 10px;
}

.button-section {
  text-align: right;
  padding: 10px;
  position: absolute;
  right: 0;
}

.text-form {
  padding: 20px;
  text-align: center;
}

input {
  padding: 10px;
  flex-grow: 1;
  outline: none;
  box-sizing: border-box;
  vertical-align: top;
}

button {
  cursor: pointer;
  padding: 10px 20px;
  background-color: yellowgreen;
  color: #fff;
  border: none;
  outline: none;
  flex-shrink: 0;
  margin-left: 10px;
}

span {
  cursor: pointer;
}

.container {
  width: 300px;
}

.item {
  width: 100px;
  height: 100px;
  border: 4px solid #000;
  background-color: orange;
  animation: move 1s infinite alternate ease-out;
}

