  0% {transform: translateX(0);background-color: aquamarine;}
  50% {margin-top:50px;}
  100% {transform: translateX(100px);background-color: magenta;}
}*/
.modal {
  margin-top: 20px;
  padding: 20px;
  background: #eee;
  text-align: left;
  background-color: var(--color-orange);
}